@charset "UTF-8";
.calendar {
  margin: 0 10%; }
  .calendar p {
    color: #222;
    margin: 0 5%;
    margin-top: 20px; }

.loading {
  text-align: center; }

.loading:after {
  overflow: hidden;
  display: inline-block;
  vertical-align: bottom;
  -webkit-animation: ellipsis steps(4, end) 900ms infinite;
  animation: ellipsis steps(4, end) 900ms infinite;
  content: "…";
  width: 0px; }

@keyframes ellipsis {
  to {
    width: 1.25em; } }

@-webkit-keyframes ellipsis {
  to {
    width: 1.25em; } }



